import React from 'react'

class ErrorPage extends React.Component {
  static getInitialProps({ res, err }) {
    const statusCode = res ? res.statusCode : err ? err.statusCode : null
    return { statusCode }
  }

  render() {
    return (
      <div className="error-page">
        <img />
        <p>ASD</p>
      </div>
    )
  }
}

export default ErrorPage